@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root{
  background-color: #fafafa;
}


.app{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  font-family: 'Montserrat',sans-serif;
  flex-direction: column;
  width: 90vw;
  margin: auto;
}

.select_container{
 margin: 20px;
 width: 100%;
}

.poster_image{
  width: 350px;
  height: 350px;
  object-fit: cover;
}

.movie_choosed{
  margin-bottom: 20px;
  padding-bottom: 10px;
}

.title_container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-end;
  padding:10px
}
.director_image{
  width:40px;
  height: 40px;
  margin-right: 10px;

}

.title{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.logo{
  width:50px;
  height: 50px;
  margin-left: 15px;
}
